$tab-item-border: lighten($FrenchGray, 10%);
$tab-item-bg: lighten($FrenchGray, 20%);
%box {
	background: $White;
	border-radius: 6px;
	box-shadow: 0px 5px 70px -20px rgba(0, 0, 0, 0.3);
	padding: $lg;
	margin-bottom: $lg;
}

.o-account {
	display: flex;
  justify-content: center;
  width: 100%;
	&__title {
		margin-bottom: $md;
	}
	&__message {
		font-size: $fs-16;
		&.-success {
			strong {
				color: $Niagara;
			}
		}
		&.-error {
			strong {
				color: $Monza;
			}
		}
	}
}
.recaptcha-terms-text {
	font-size: $fs-13;
	color: $DustyGray;
	a {
		color: $Emperor;
	}
}
.o-accountTab {
	display: flex;
  flex-direction: column;
  width: 100%;
	$accountTab: &;

	[data-loginTab]:checked ~ section .loginTab,
	[data-registerTab]:checked ~ section .registerTab,
	[data-guestTab]:checked ~ section .guestTab {
		display: block;
	}

	[data-loginTab]:checked ~ #{$accountTab}__nav .loginTab,
	[data-registerTab]:checked ~ #{$accountTab}__nav .registerTab,
	[data-guestTab]:checked ~ #{$accountTab}__nav .guestTab {
		border-bottom: solid 2px $Niagara;
    color: $Niagara;
	}

	&__nav {
    $accountTabNav: &;
    background-color: $Porcelain;
    display: flex;
		&--title {
      flex: 0 0 50%;
      max-width: 50%;
			font-size: $fs-16;
      text-align: center;
      padding: $md 0;
      font-weight: $bold;
		}
	}
	&__section {
		background: $White;
    padding: $lg;
    
		.tab-pane {
			display: none;
		}
	}
}
